import { ListSorterDirection } from "@rp/common/utils"
import { IconButton, MenuItem, Select, styled } from "@rp/react/web/mui/core"
import { ArrowDownward } from "@rp/react/web/mui/icons"
import { SortOption } from "./hubTable.types"

const SelectStyled = styled("div")(() => ({
  display: "flex",
  " .MuiInputBase-root": {
    margin: 0,
    padding: "0px",
  },
  "	.MuiSelect-icon": {
    display: "none",
  },
  " .MuiSelect-select": {
    paddingRight: "0 !important",
  },
}))

function HubSortableHeaderMobileCell<T>({
  sortBy,
  direction,
  onSortChange,
  onDirectionChange,
  sortOptions,
}: {
  direction: ListSorterDirection
  sortBy: keyof T
  sortOptions: Array<SortOption>
  onSortChange?: (sortBy: keyof T) => void
  onDirectionChange?: (direction: ListSorterDirection) => void
}) {
  return (
    <SelectStyled>
      <Select
        variant="standard"
        disableUnderline
        value={sortBy ?? sortOptions[0].value}
        sx={{ margin: "12px 0", color: "text.secondary" }}
        onChange={ev => {
          onSortChange?.(ev.target.value as keyof T)
        }}
        MenuProps={{
          sx: theme => ({
            ul: {
              backgroundColor: "elevation.dp06",
              fontSize: "2px !important",
            },
            ".Mui-selected": {
              color: "primary.main",
              backgroundColor: `${theme.palette.elevation.dp06} !important`,
              "&:hover": {
                backgroundColor: "overlay.surface",
              },
            },
          }),
        }}
      >
        {sortOptions.map(({ label, value }) => (
          <MenuItem key={value} value={value}>
            {label}
          </MenuItem>
        ))}
      </Select>
      <IconButton
        onClick={() => {
          onDirectionChange?.(direction === "asc" ? "desc" : "asc")
        }}
        size="small"
      >
        <ArrowDownward
          sx={{
            transform: `rotate(${direction === "desc" ? 0 : 180}deg)`,
            color: "text.secondary",
            fontSize: "20px",
          }}
        />
      </IconButton>
    </SelectStyled>
  )
}

export { HubSortableHeaderMobileCell }
